@import url("https://fonts.googleapis.com/css2?family=Barlow&family=Inter+Tight&family=Work+Sans&display=swap");
* {
  text-align: center;
  justify-content: center;
  font-family: "Inter Tight", sans-serif;
  box-sizing: border-box;
}

.main-title {
  color: rgb(0, 150, 255);
  font-size: 3rem;
}

.task-list {
  padding-right: 5vh;
  list-style-type: none;
  cursor: pointer;
  margin: 0 35vw;
}

.task-icons {
  padding: 0 5px;
  font-size: 20px;
}

.list-items {
  display: flex;
  border: 2px rgb(0, 150, 255) solid;
  border-radius: 5px;
  margin: 15px 0;
  align-items: center;
}
.list-items li {
  margin: 0px 15px;
  font-family: "Barlow", sans-serif;
  font-size: 28px;
  height: auto;
  padding-bottom: 0.5vh;
}

.check {
  color: rgb(0, 150, 255);
}

.remove {
  color: red;
}

.priority {
  padding-bottom: 0.3vh;
}

#input {
  text-align: left;
  height: 6vh;
  border-radius: 5px;
  width: 25vw;
  font-size: 0.7rem;
}

#input-label {
  font-weight: 600;
}

.input-btns {
  display: flex;
}

button {
  color: white;
  background-color: rgb(0, 150, 255);
  border: 1.5px black solid;
  font-size: 0.75em;
  border-radius: 5px;
}

.num-tasks {
  font-family: "Work Sans", sans-serif;
}

.task-number {
  color: rgb(0, 150, 255);
}

.completed {
  display: flex;
}
.completed p {
  font-family: "Inter Tight", sans-serif;
  font-size: 30px;
}

.completed-bar {
  border: 1px grey solid;
}

.list-arrows {
  margin-top: 6vh;
  padding-left: 20px;
  font-size: 28px;
  cursor: pointer;
}

.clear-completed {
  margin-top: 6.5vh;
  padding-left: 10px;
  font-size: 20px;
  cursor: pointer;
}

.complete-list-items {
  display: flex;
  list-style-type: none;
}
.complete-list-items li {
  text-decoration: line-through;
  margin: 0px 15px;
  font-size: 28px;
  font-family: "Barlow", sans-serif;
}

.undo-task {
  font-size: 24px;
  padding-top: 0.7vh;
  color: rgb(92, 163, 98);
  cursor: pointer;
}

.complete-list {
  display: none;
}

.completed-num {
  color: rgb(92, 163, 98);
}

.sub-task-items {
  display: flex;
}

.sub-task-lists {
  list-style-type: none;
}

@media screen and (max-width: 450px) {
  .main-title {
    font-size: 2rem;
  }
  .completed-bar {
    margin: 0;
  }
  #input {
    transform: scale(120%);
    margin-top: 3.2px;
  }
  button {
    margin-left: 3vw;
    height: 5.5vh;
  }
  .list-arrows {
    margin-top: 3.2vh;
    font-size: 30px;
  }
  .clear-completed {
    margin-top: 3.8vh;
  }
  .list-items li,
  .complete-list li {
    font-size: 1.1rem;
  }
  .task-list {
    margin: 0;
  }
  .task-icons {
    font-size: 18px;
  }
  .completed p {
    font-size: 26px;
  }
  .undo-task {
    font-size: 18px;
    padding-top: 0.5vh;
  }
}
@media screen and (min-width: 450px) and (max-width: 1024px) {
  body {
    font-size: 1.5rem;
  }
  #input {
    transform: scale(150%);
    margin-top: 11px;
  }
  button {
    height: 8.5vh;
    margin-left: 6vw;
  }
  .completed-bar {
    margin: 0;
  }
  .list-items li,
  .complete-list-items li {
    font-size: 1.8rem;
  }
  .completed,
  .completed p {
    font-size: 2rem;
  }
  .list-arrows {
    margin-top: 7vh;
    font-size: 48px;
  }
  .clear-completed {
    margin-top: 7.2vh;
    font-size: 36px;
  }
  .undo-task {
    font-size: 32px;
    padding-top: 0.5vh;
  }
  .task-list {
    margin: 0 10vw;
  }
  .task-icons {
    font-size: 26px;
  }
  .list-items {
    margin: 35px 0;
  }
}/*# sourceMappingURL=App.css.map */